@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, "Montserrat", sans-serif,
    "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #222629 !important;
  color: #ffffff !important;

  /* background-color: #37474f !important;
  color: #fff; */
}
html,
body {
  height: 100%;
  scroll-behavior: smooth;
}

#root {
  height: 100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-container {
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  line-height: 1.5em;
}

.nav-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}
a {
  color: white;
  text-decoration: none;
}

.myportfolio-container {
  height: 100%;
  position: relative;
}

.separator {
  font-size: 22px;
  text-align: center;
  line-height: 22px;
}
.separator span {
  position: relative;
}
.separator span:before,
.separator span:after {
  content: "";
  position: absolute;
  height: 6px;
  border-bottom: 2px solid #ffffff;
  top: 10px;
  width: 40px;
}
.separator span:before {
  right: 100%;
  margin-right: 8px;
}
.separator span:after {
  left: 100%;
  margin-left: 8px;
}

.section-heading p {
  font-size: 2em;
  color: #86c232;
  text-align: center;
}

.active {
  color: #86c232;
}
.section-heading {
  padding: 40px 0;
}

header {
  position: relative;
  top: 0;
  transition: background-color 0.3s, opacity 0.2s, visibility 0s;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  z-index: 1;
  background-color: #37474f !important;
  min-height: 100% !important;
  height: auto;
  background: url(/my-portfolio/static/media/moon.7672908d.jpg) no-repeat;
  background-size: cover;
  background-position: center center;
}
.tagline {
  height: 100vh;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.tag h5 {
  color: #fff;
  font-weight: 700;
  font-size: 16px;
  word-wrap: wrap;
  text-align: center;
}
.tag {
  position: absolute;
  top: 50;
}
.sub p {
  color: #6b6e70;
  font-weight: 600;
}

.about-container {
  /* min-height: calc(100% - 120px) !important; */
  height: auto;
  padding: 2vh 11.805%;
}

.about-content {
  display: flex;
  justify-content: space-between;
  margin: 10px 40px;
}
.about-content > div {
  flex: 1 1;
}

.about-content .about-img {
  align-self: center;
  justify-self: center;
}
.about-content .about-img img {
  max-width: 400px;
  height: 400px;
}

.social-links-left {
  align-self: center;
}
.my_links {
  transition: color 500ms ease;
}
.my_links:hover {
  color: #86c232;
}
.my_links span {
  display: flex;
  align-items: center;
}

.my_links span > * {
  margin: 5px;
}

@media only screen and (max-width: 799px) {
  .social-links-left {
    align-self: flex-start;
    padding-top: 20px;
  }
  .about-3d,
  .about-content {
    margin: 2rem 0;
    flex-direction: column;
  }
  .about-3d img {
    width: 250px;
  }
}

.side-nav {
  display: none;
}

.logo a {
  color: #86c232;
  font-weight: 700;
}
.nav-container {
  z-index: 99;
  display: flex;
  justify-content: space-around;
  padding: 15px;
  background-color: #222629;
  /* top: 0; */
  width: 100%;
  color: white;
  border-bottom: 2px solid #61892f;
}

.nav .nav-items {
  list-style: none;
  display: flex;
}

.nav .nav-items li {
  margin: 0 30px;
}

