} */

.nav .nav-items li a {
  text-decoration: none;
  color: white;
}

.hamburger {
  display: none;
  cursor: pointer;
}

.tagline h1 {
  font-weight: 300;
  line-height: 2em;
}
.active {
  color: #86c232 !important;
}

@media only screen and (max-width: 720px) {
  #menuToggle {
    display: block;
  }
  .nav-container {
    margin: 0;
    justify-content: space-between;
  }
  .nav {
    display: none;
  }

  .hamburgerMenu {
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
  }
  .side-nav {
    position: relative;
    display: flex;
    flex-direction: column;
    position: fixed;
    justify-content: center;
    align-items: center;
    width: 60%;
    right: 0;
    top: 0;
    margin: 0;
    transform: translateX(100%);
    height: calc(100% + 60px);
    transition: transform 0.5s ease-in-out;
    padding-bottom: 60px;
    background-color: #222629;
    z-index: 999;
    overflow-y: auto;
    will-change: transform;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    list-style: none;
  }

  .side-nav li {
    margin: 2rem;
  }

  .side-nav li::after {
    content: "";
    display: block;
    padding: 1px;

    width: 0px;
    height: 0px;
    transition: all 0.5s ease;
  }

  .side-nav li:hover::after {
    background-color: #61892f;
    width: 100%;
    height: 2px;
  }

  .side-nav li a {
    text-decoration: none;

    color: #fff;
  }
  .side-nav.toggle {
    transform: translateX(0px);
  }
  .hamburger {
    display: block;
  }
}

#contact {
  position: absolute;
  top: 50;
  right: 0;
  z-index: 3;
}

.social-links {
  display: flex;
  flex-direction: column;
}

.social-links a:hover {
  color: #86c232;
}

.footer-container {
  clear: both;
  height: 20px;
  font-size: 0.8em;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

.footer-contact {
  display: flex;
  justify-content: center;
}
.footer-contact a {
  margin: 0 10px;
}
.footer-contact a:hover {
