}

.projects {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-auto-rows: minmax(200px, auto);
  grid-gap: 3px;
  gap: 3px;
}

.project-container {
  padding: 2vh 11.805%;
  height: auto !important;
}

@media only screen and (max-width: 600px) {
  .projects {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-auto-rows: minmax(150px, auto);
    grid-gap: 20px;
    gap: 20px;
  }
  .hrBorder {
    width: 280px;
  }
}

.card {
  display: flex;
  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); */
  height: 100%;
}

.card .card-content {
  /* border-radius: 5px; */
  position: relative;
  /* box-shadow: 0 14px 28px rgba(255, 255, 255, 0.25),
    0 10px 10px rgba(255, 255, 255, 0.22); */

  background-color: #ffffff;
  color: black;
  /* box-shadow: 0px 0px 115px #000000a6; */
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;
  overflow: hidden;
}

.card-info img {
  transition: all 0.4s ease;
}
.card .card-content:hover .card-heading {
  opacity: 1;
}

.card-info {
  height: 100%;
}

.card-heading {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #ffffff;
  transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
  background-color: rgba(0, 104, 183, 0.9);
  /* background-color: #212121; */
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 5;
  opacity: 0;
}

